'use client';

import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Separator } from "@/components/ui/separator";
import { MessageSquare, ThumbsUp, Share2, Volume2 } from "lucide-react";
import { useSpeech } from "@/hooks/use-speech";
import { useLanguage } from "@/contexts/language-context";

const posts = [
  {
    id: 1,
    author: "B. N. Rao",
    avatar: "https://picsum.photos/seed/user1/40/40",
    time: "2 hours ago",
    content: "Just tried the new crop rotation suggestion from Mannova. My soil already feels healthier! Has anyone else seen good results?",
    image: "https://picsum.photos/seed/post1/600/400",
    imageHint: "healthy soil",
    likes: 15,
    comments: 4,
  },
  {
    id: 2,
    author: "C. Devi",
    avatar: "https://picsum.photos/seed/user2/40/40",
    time: "5 hours ago",
    content: "Harvested my first batch of organic tomatoes today. The taste is incredible. Thank you to this community for all the tips on composting!",
    image: "https://picsum.photos/seed/post2/600/400",
    imageHint: "organic tomatoes",
    likes: 32,
    comments: 8,
  },
];

export default function CommunityPage() {
    const { speak, isSpeaking } = useSpeech();
    const { language } = useLanguage();

    const handleSpeak = (text: string) => {
        speak({ text, lang: language });
    };

  return (
    <div className="space-y-6 max-w-3xl mx-auto">
      <Card>
        <CardHeader>
          <CardTitle>Community Forum</CardTitle>
          <CardDescription>Share tips, ask questions, and connect with fellow farmers.</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex gap-4">
            <Avatar>
              <AvatarImage src="https://picsum.photos/seed/farmer/40/40" />
              <AvatarFallback>MN</AvatarFallback>
            </Avatar>
            <Input placeholder="What's on your mind, farmer?" className="flex-grow"/>
            <Button>Post</Button>
          </div>
        </CardContent>
      </Card>
      
      <div className="space-y-6">
        {posts.map(post => (
          <Card key={post.id}>
            <CardHeader className="flex flex-row items-center justify-between">
                <div className="flex items-center gap-4">
                    <Avatar>
                        <AvatarImage src={post.avatar} />
                        <AvatarFallback>{post.author.charAt(0)}</AvatarFallback>
                    </Avatar>
                    <div>
                        <p className="font-semibold">{post.author}</p>
                        <p className="text-xs text-muted-foreground">{post.time}</p>
                    </div>
                </div>
                <Button variant="outline" size="icon" onClick={() => handleSpeak(`Post by ${post.author}. ${post.content}`)} disabled={isSpeaking}>
                    <Volume2 className="h-4 w-4" />
                    <span className="sr-only">Read post</span>
                </Button>
            </CardHeader>
            <CardContent>
              <p className="mb-4">{post.content}</p>
              {post.image && (
                 <div className="relative w-full aspect-video rounded-lg overflow-hidden">
                    <img src={post.image} alt="Post content" className="object-cover w-full h-full" data-ai-hint={post.imageHint} />
                 </div>
              )}
            </CardContent>
            <Separator className="my-2"/>
            <CardFooter className="flex justify-around p-2">
              <Button variant="ghost" className="flex items-center gap-2">
                <ThumbsUp className="h-4 w-4"/> {post.likes} Likes
              </Button>
              <Button variant="ghost" className="flex items-center gap-2">
                <MessageSquare className="h-4 w-4"/> {post.comments} Comments
              </Button>
              <Button variant="ghost" className="flex items-center gap-2">
                <Share2 className="h-4 w-4"/> Share
              </Button>
            </CardFooter>
          </Card>
        ))}
      </div>
    </div>
  );
}
